import { number } from "echarts";
var dayjs = require('dayjs')

export default{
    namespaced: true,
    state: { 
       dataAll:JSON.parse(localStorage.getItem('m') ||`[{"title":"","items":[{"day":"","week":"","payAll":0,"incomeAll":0,"list":[{"id":"","icon":"","kind":"","amount":0}]}]}]`) ,
        dateShow:false,
        currentDate:new Date(),
        titleTime:`${dayjs(new Date()).format('YYYYMM')}`,
        showTile:0,//dataAll的第几项数据，打开应用默认打开最后一次记录的那个月
        monthPay:0,
        monthIncome:0,
        isSHowListPage:false,
        isShowPayList:true
    },
    getters:{
      //默认首次进入页面展示的月份数据，默认为已记录的最新数据
      dataShow(state: { dataAll: { [x: string]: any }; showTile: string | number; titleTime: any }){
        let showMonth=state.dataAll[state.showTile]   
       return showMonth
      },
    },
    mutations: { 
      //计算选择月份的总收入和总支出
      reCount(state: { dataAll: { [x: string]: { items: any[] } }; showTile: string | number; monthIncome: number; monthPay: number }){
        state.dataAll[state.showTile].items.forEach((day: { incomeAll: number; payAll: number; list: any[] })=>{       
        day.incomeAll=0
        day.payAll=0
        day.list.forEach((list: { amount: number })=>{    
         if(list.amount>0){
           day.incomeAll = (day.incomeAll*10+list.amount*10)/10
           //@ts-ignore
           day.incomeAll=day.incomeAll.toFixed(2)
          }else{
           day.payAll = (day.payAll*10+list.amount*10)/10
           //@ts-ignore
           day.payAll=day.payAll.toFixed(2)
         }
      })
    })
        state.monthIncome=0
        state.monthPay=0
        state.dataAll[state.showTile].items.forEach((day: { incomeAll: any; payAll: any })=>{
          //@ts-ignore
          state.monthIncome = (((state.monthIncome*10+day.incomeAll*10)/10)).toFixed(2)
          //@ts-ignore
          state.monthPay =((state.monthPay*10+day.payAll*10)/10 ).toFixed(2)
          
         })
           },
           
      sortDataAll(state: { dataAll: any[]; }){
        state.dataAll.forEach(lists=>{
        lists.items.sort((a: { day: number; },b: { day: number; })=>{return b.day-a.day})
            })
        state.dataAll.sort((a,b)=>{return b.title-a.title})
        localStorage.setItem('m',JSON.stringify(state.dataAll))
          },
      putLocalStorage(state: { dataAll: any; }){
            localStorage.setItem('m',JSON.stringify(state.dataAll))
          },
      },
    actions: {
    }
}  